@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-duotone.ttf);
  font-weight: 900;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-light.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "fontawesome";
  src: url(../fonts/fontawesome/fontawesome-brands.ttf);
  font-weight: 300;
}

@font-face {
  font-family: "Roboto";
  src: url(../fonts/Roboto/Roboto-Regular.ttf);
  font-weight: 400;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #292929;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

body {
  font-family: 'Roboto';
}

img {
  display: block;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (max-width: 1140px) {
  .container {
    max-width: 992px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 768px) {
  .container {
    max-width: 576px;
  }
}

@media (max-width: 576px) {
  .container {
    max-width: 100%;
  }
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

::after, ::before {
  font-family: 'fontawesome';
}

.icon-shop::before {
  content: "\f07a";
  color: #000;
}

.icon-insta::before {
  content: "\f16d";
  color: #000;
}

.icon-facebook::before {
  content: "\f082";
  color: #000;
}

.icon-twitter::before {
  content: "\f099";
  color: #000;
}

.icon-profil::before {
  content: "\f007";
  color: #000;
}

.sale {
  background: #ced3ca;
  padding: 9px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 576px) {
  .sale {
    flex-direction: column;
    height: 100%;
    padding: 4px;
  }
}

.sale__link {
  display: block;
  font-size: 12px;
  color: #fff;
  background: #000;
  padding: 7px 17px;
}

@media (max-width: 576px) {
  .sale__link {
    margin-top: 7px;
  }
}

.sale__text {
  font-size: 14px;
  margin-right: 16px;
}

body.lock {
  overflow: hidden;
}

.nav::before {
  content: '';
  width: 100%;
  height: 100%;
  display: block;
}

.nav__menu {
  margin-left: -200px;
}

@media (max-width: 992px) {
  .nav__menu {
    right: 0;
    top: -100%;
    position: fixed;
    height: 100%;
    width: 100%;
    background: rgba(0, 0, 0, 0.699);
    transition: 0.5s;
    z-index: 99;
  }
  .nav__menu.active {
    top: 0;
  }
}

.nav__body li {
  margin-right: 32px;
}

@media (max-width: 992px) {
  .nav__body li {
    margin: 0;
    margin-bottom: 15px;
  }
}

@media (max-width: 1140px) {
  .nav__icon::before {
    color: #fff;
  }
}

.nav__link {
  color: #292929;
  font-size: 16px;
}

@media (max-width: 992px) {
  .nav__link {
    color: #fff;
    font-size: 35px;
  }
}

.nav__container {
  max-width: 1220px;
}

.nav__content {
  height: 115px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 992px) {
  .nav__content {
    height: 80px;
  }
}

@media (max-width: 992px) {
  .nav__logo img {
    height: 30px;
  }
}

@media (max-width: 768px) {
  .nav__logo img {
    height: 20px;
  }
}

.nav__body {
  display: flex;
}

@media (max-width: 992px) {
  .nav__body {
    margin-top: 100px;
    flex-direction: column;
    align-items: center;
  }
}

.nav-auth {
  display: flex;
  z-index: 10;
}

.nav-auth.disp-block {
  display: flex;
}

@media (max-width: 1140px) {
  .nav-auth {
    position: absolute;
    right: 30px;
    top: 120px;
    flex-direction: column;
    width: 150px;
    background: rgba(0, 0, 0, 0.699);
    padding: 15px 20px;
    display: none;
  }
}

@media (max-width: 992px) {
  .nav-auth {
    top: 100px;
  }
}

@media (max-width: 576px) {
  .nav-auth {
    top: 120px;
  }
}

.nav-auth__link {
  color: #292929;
}

@media (max-width: 1140px) {
  .nav-auth__link {
    margin-bottom: 10px;
    color: #fff;
    font-size: 25px;
  }
}

.nav-auth__link:nth-child(3) {
  margin-right: 30px;
}

.nav-line {
  height: 20px;
  width: 1px;
  background: #000;
  margin: 0 12px;
}

@media (max-width: 1140px) {
  .nav-line {
    display: none;
  }
}

.nav-profil {
  display: none;
}

@media (max-width: 1140px) {
  .nav-profil {
    display: block;
  }
}

.nav__burger {
  height: 12px;
  width: 25px;
  position: relative;
  display: none;
  z-index: 3;
}

@media (max-width: 992px) {
  .nav__burger {
    display: block;
    z-index: 100;
  }
  .nav__burger.active::before {
    transform: rotate(45deg);
    top: 5px;
    background: #fff;
  }
  .nav__burger.active::after {
    transform: rotate(-45deg);
    bottom: 5px;
    background: #fff;
  }
}

.nav__burger::before, .nav__burger::after {
  content: '';
  display: block;
  height: 2px;
  position: absolute;
  left: 0;
  width: 100%;
  background: #000;
  transition: 0.5s;
}

.nav__burger::before {
  top: 0;
}

.nav__burger::after {
  bottom: 0;
}

.banner {
  width: 100%;
  position: relative;
  z-index: 3;
  overflow: hidden;
}

.banner .slick-track {
  display: flex;
}

.banner .slick-list {
  overflow: hidden;
}

.slide__img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.slide {
  height: 70vh;
}

@media (max-width: 992px) {
  .slide {
    height: 50vh;
  }
}

.slick-arrow {
  font-size: 0;
  opacity: 0;
  height: 80px;
  width: 30px;
  background: rgba(255, 255, 255, 0.219);
  z-index: 10;
  position: absolute;
  top: 45%;
  border: none;
  outline: none;
  transition: 0.5s;
}

.banner:hover .slick-arrow {
  opacity: 1;
}

.slick-next {
  right: 0;
}

.products {
  padding: 40px 0 170px 0;
}

.products__section {
  display: none !important;
}

.products__section.active {
  display: grid !important;
}

.products__list {
  display: flex;
  justify-content: center;
  margin-bottom: 50px;
  flex-wrap: wrap;
}

.products__list li {
  margin: 0 25px;
  margin-bottom: 10px;
}

.products__link {
  color: #ced3ca;
  cursor: pointer;
}

.products__link.active {
  color: #292929;
}

.products__section {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 30px;
}

@media (max-width: 1140px) {
  .products__section {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 768px) {
  .products__section {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 576px) {
  .products__section {
    grid-template-columns: repeat(1, 0.8fr);
    justify-content: center;
  }
}

.products__btn {
  display: block;
  width: 300px;
  height: 56px;
  background: #292929;
  color: white;
  margin: auto;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 65px;
}

@media (max-width: 768px) {
  .products__btn {
    width: 200px;
    height: 40px;
    font-size: 15px;
  }
}

.product {
  display: flex;
  flex-direction: column;
}

.product__img {
  flex-grow: 1;
}

.product__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product__title {
  margin-top: 10px;
  font-size: 15px;
  font-weight: 400;
  margin-bottom: 6px;
}

.product__price {
  font-size: 13px;
}

.stock {
  padding: 127px 0px;
  background: #ced3ca;
}

@media (max-width: 992px) {
  .stock {
    padding: 70px 90px;
  }
}

@media (max-width: 768px) {
  .stock {
    padding: 20px 30px;
  }
}

.stock__main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #F7F7F7;
}

@media (max-width: 1140px) {
  .stock__main {
    flex-direction: column;
  }
}

.stock__info {
  margin-left: 90px;
}

@media (max-width: 1140px) {
  .stock__info {
    margin: 0;
    text-align: center;
    margin-top: 30px;
    margin-bottom: 30px;
  }
}

.stock__title {
  font-size: 38px;
  font-weight: 400;
  line-height: 45px;
  margin-bottom: 30px;
}

.stock__link {
  position: relative;
  font-size: 14px;
}

.stock__link::after {
  content: '';
  width: 100px;
  height: 1px;
  background: #292929;
  position: absolute;
  left: 0;
  top: 25px;
}

.stock__img {
  max-width: 754px;
}

.stock__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.missions {
  padding: 115px 0;
}

.missions__main {
  display: grid;
  grid-gap: 90px 116px;
  grid-template: [start] "title title" [row] [row] "men women" [row2] [row2] "obj obj" [end];
}

@media (max-width: 992px) {
  .missions__main {
    grid-gap: 50px 30px;
  }
}

@media (max-width: 576px) {
  .missions__main {
    grid-template: [start] "title title" "men men" "women women" "obj obj";
  }
}

.missions__title {
  grid-area: title;
  text-align: center;
  font-size: 30px;
  font-weight: 400;
}

@media (max-width: 992px) {
  .missions__title {
    font-size: 20px;
  }
}

.mission {
  display: flex;
  flex-direction: column;
}

.mission__men {
  grid-area: men;
}

.mission__women {
  grid-area: women;
}

.mission__obj {
  grid-area: obj;
}

@media (max-width: 992px) {
  .mission__title {
    font-size: 13px;
  }
}

.mission__img {
  flex-grow: 1;
}

.mission__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.gather {
  padding: 127px 0;
  background: #ced3ca;
}

@media (max-width: 992px) {
  .gather {
    padding: 70px 90px;
  }
}

@media (max-width: 768px) {
  .gather {
    padding: 20px 30px;
  }
}

.gather__block {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #fff;
}

@media (max-width: 1140px) {
  .gather__block {
    flex-direction: column;
  }
}

.gather__info {
  max-width: 210px;
  margin-left: 90px;
}

@media (max-width: 1140px) {
  .gather__info {
    margin-bottom: 30px;
    margin-top: 30px;
    margin-left: 0;
    text-align: center;
    max-width: 100%;
  }
}

.gather__title {
  font-size: 37px;
  font-weight: 400;
  margin-bottom: 20px;
}

.gather__text {
  font-size: 15px;
  margin-bottom: 32px;
}

.gather__img {
  max-width: 800px;
}

@media (max-width: 1140px) {
  .gather__img {
    width: 100%;
  }
}

.gather__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.studio {
  background: url("../images/bg.png") center center/cover;
  background-attachment: fixed;
  max-width: 100%;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: 992px) {
  .studio {
    height: 400px;
  }
}

.studio__info {
  max-width: 782px;
  text-align: center;
}

.studio__title {
  color: white;
  font-size: 35px;
}

@media (max-width: 992px) {
  .studio__title {
    font-size: 20px;
  }
}

.studio__link {
  font-size: 15px;
  color: white;
}

.footer {
  padding: 30px 0;
  text-align: center;
  background: #ced3ca;
}

.footer__title {
  font-size: 20px;
}
