.gather {
    padding: 127px 0;
    background: $gray-bg;


    @include media($md) {
        padding: 70px 90px;
    }
    @include media($sm) {
        padding: 20px 30px;
    }


    &__block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: $white;

        @include media($lg) {
            flex-direction: column;
        }
    }

    &__info {
        max-width: 210px;
        margin-left: 90px;

        @include media($lg) {
            margin-bottom: 30px;
            margin-top: 30px;
            margin-left: 0;
            text-align: center;
            max-width: 100%;
        }
    }

    &__title {
        font-size: 37px;
        font-weight: 400;
        margin-bottom: 20px;

    }

    &__text {
        font-size: 15px;
        margin-bottom: 32px;
    }


    &__img {
        max-width: 800px;

        @include media($lg) {
            width: 100%;
        }


    }

    &__img img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        

    }


}