.sale {
    background: $gray-bg;
    padding: 9px;
    display: flex;
    align-items: center;
    justify-content: center;

    @include media($xs) {
        flex-direction: column;
        height: 100%;
        padding: 4px;
    }

    &__link {
        display: block;
        font-size: 12px;
        color: $white;
        background: $black;
        padding: 7px 17px;

        @include media($xs) {
            margin-top: 7px;
        }
    }

    &__text {
        font-size: 14px;
        margin-right: 16px;
    }
}