.stock {
    padding: 127px 0px;
    background: $gray-bg;

    @include media($md) {
        padding: 70px 90px;
    }
    @include media($sm) {
        padding: 20px 30px;
    }




    &__main {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #F7F7F7;

        @include media($lg) {
            flex-direction: column;
        }
    }

    &__info {
        margin-left: 90px;
        
        @include media($lg) {
            margin: 0;
            text-align: center;
            margin-top: 30px;
            margin-bottom: 30px;
        }
    }

    &__title {
        font-size: 38px;
        font-weight: 400;
        line-height: 45px;
        margin-bottom: 30px;

    }

    &__link {
        position: relative;
        font-size: 14px;

    }

    &__link::after {
        content: '';
        width: 100px;
        height: 1px;
        background: $black-font;
        position: absolute;
        left: 0;
        top: 25px;
    }

    &__img {
        max-width: 754px;

    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}