*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: $black-font;
}

a{
    text-decoration: none;
}

ul {
    list-style: none;
}

body {
    font-family: 'Roboto';
    // color: $black-font
}

img {
    display: block;
}

.container {
    @include container();
}

.row {
    @include row();
}

::after, ::before {
    font-family: 'fontawesome';
}

.icon {
    &-shop::before {
        content: "\f07a";
        color: $black;
    }
    &-insta::before {
        content: "\f16d";
        color: $black;
    }
    &-facebook::before {
        content: "\f082";
        color: $black;
    }
    &-twitter::before {
        content: "\f099";
        color: $black;
    }
    &-profil::before {
        content: "\f007";
        color: $black;
    }

}