.products {
    padding: 40px 0 170px 0;


    &__section {
        display: none !important;

        &.active {
            display: grid !important;
        }
    }


    &__list {
        display: flex;
        justify-content: center;
        margin-bottom: 50px;
        flex-wrap: wrap;
        

        & li {
            margin:0 25px;
            margin-bottom: 10px;
        }
    }

    &__link {
        color: $gray-bg;
        cursor:pointer;

        &.active {
            color: $black-font;
        }
    }

    &__section {
        display: grid;
        grid-template-columns: repeat(4, 1fr );
        grid-column-gap: 20px;
        grid-row-gap: 30px;

        @include media($lg) {
            grid-template-columns: repeat(3, 1fr);
        }
        @include media($sm) {
            grid-template-columns: repeat(2, 1fr);
        }
        @include media($xs) {
            grid-template-columns: repeat(1, 0.8fr);
            // justify-items: center;
            justify-content: center;
        }
    }

    &__btn {
        display: block;
        // padding: 15px 60px;
        width: 300px;
        height: 56px;
        background: $black-font;
        color: $gray-font;
        margin: auto;
        font-size: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 65px;


        @include media($sm) {
            width: 200px;
            height: 40px;
            font-size: 15px;
        }

    }

    
}

.product {
    display: flex;
    flex-direction: column;
    
    
    
    &__img {
        flex-grow: 1;
    }
    
    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    

    &__title {
        margin-top: 10px;
        font-size: 15px;
        font-weight: 400;
        margin-bottom: 6px;
    }

    &__price {
        font-size: 13px;
    }
}