.missions {
    padding: 115px 0;

    &__main {
        display: grid;
        grid-gap: 90px 116px;

        grid-template:  [start] "title title" [row]
                        [row] "men women" [row2]
                        [row2] "obj obj" [end];

        @include media($md) {
            grid-gap: 50px 30px;

        }

        @include media($xs) {
            grid-template: [start] "title title"
                                    "men men"
                                    "women women"
                                    "obj obj"
                                    ;
        }
            
    }

    &__title {
        grid-area: title;
        text-align: center;
        font-size: 30px;
        font-weight: 400;

        @include media($md) {
            font-size: 20px;
            
        }
    }
}

.mission {
    display: flex;
    flex-direction: column;

    &__men {
        grid-area: men;
    }

    &__women {
        grid-area: women;
    }
    
    &__obj {
        grid-area: obj;
    }

    &__title {
        @include media($md) {
            font-size: 13px;
            
        }
    }



    &__img {
        flex-grow: 1;
    }

    &__img img{
        width: 100%;
        height: 100%;
        object-fit: cover;

    }
}