$Montserrat:('fontawesome','duotone', 900),
            ('fontawesome','light', 300),
            ('fontawesome','brands', 300),
            ('Roboto','Regular', 400),
            ;

@each $family, $name, $weight in $Montserrat {
    @font-face {
        font-family: $family;
        src: url(../fonts/#{$family}/#{$family}-#{$name}.ttf);
        font-weight: $weight;
    }
}

$white: #fff;
$black: #000;
$gray-bg: rgba(206, 211, 202, 1);
$black-font: rgba(41, 41, 41, 1);
$gray-font: rgba(255, 255, 255, 1);
$unactive_btn: rgba(179, 179, 179, 1);
$white-bg: rgba(248, 246, 242, 1);
$nav-bg: rgba(0, 0, 0, 0.699);
$finalStock-bg: #F7F7F7;