.banner {
    // height: 70vh;
    width: 100%;
    position: relative;
    z-index: 3;
    overflow: hidden;

    // @include media($md) {
    //     height: 50vh;
    // }


    .slick-track {
        display: flex;
    }

    .slick-list {
        overflow: hidden;
    }    


}

.slide__img {
    // width: 100%;
    // height: 100%;
    object-fit: cover;
    height: 100%;
    width: 100%;
}

.slide {
    height: 70vh;
    @include media($md) {
        height: 50vh;
    }
}

.slick-arrow {
    font-size: 0;
    opacity: 0;
    height: 80px;
    width: 30px;
    background: rgba(255, 255, 255, 0.219);
    z-index: 10;
    position: absolute;
    top: 45%;
    border: none;
    outline: none;
    transition: 0.5s;
}

.banner:hover .slick-arrow {
    opacity: 1;
}

.slick-next {
    right: 0;
}

