body {
    &.lock {
        overflow: hidden;
    }
}

.nav {

    &::before {
        content: '';
        width: 100%;
        height: 100%;
        display: block;

    }

    &__menu {
        margin-left: -200px;

        @include media($md) {
                right: 0;
                top: -100%;
                position: fixed;
                height: 100%;
                width: 100%;
                background: $nav-bg;
                transition: 0.5s;
                z-index: 99;
                &.active {
                    top: 0;
                }
        }

        
    }

    &__body li {
        margin-right: 32px;

        @include media($md) {
            margin: 0;
            margin-bottom: 15px;
        }



    }

    &__icon::before {
        @include media($lg) {
            color: $white;
        }
    }

    &__link {
        color: $black-font;
        font-size: 16px;

        @include media($md) {
            color: $white;
            font-size: 35px;
        }
    }

    &__container {
        max-width: 1220px;
    }

    &__content {
        height: 115px;
        display: flex;
        justify-content: space-between;
        align-items: center;


        @include media($md) {
            height: 80px;
        }
    }

    &__logo img {
        @include media($md) {
            height: 30px;
        }
        @include media($sm) {
            height: 20px;
        }
    }
    

    &__body {
        display: flex;

        @include media($md) {
            margin-top: 100px;
            flex-direction: column;
            align-items: center;
        }
       
    }

    &-auth {
        display: flex;
        z-index: 10;

        &.disp-block {
            display: flex;
        }

        @include media($lg) {
            position: absolute;
            right: 30px;
            top: 120px;
            flex-direction: column;
            width: 150px;
            background: $nav-bg;
            padding: 15px 20px;
            display: none;
        }

        @include media($md){
            top: 100px;
        }

        @include media($xs) {
            top: 120px;
        }
    }

    &-auth__link {
        color: $black-font;

        @include media($lg) {
            margin-bottom: 10px;
            color: $white;
            font-size: 25px;
        }
    }

    &-auth__link:nth-child(3) {
        margin-right: 30px;
    }

    &-line {
        height: 20px;
        width: 1px;
        background: $black;
        margin: 0 12px;

        @include media($lg) {
            display: none;
        }
    }

    &-profil {
        display: none;

        @include media($lg) {
            display: block;
        }
    }

    &__burger {
        height: 12px;
        width: 25px;
        position: relative;
        display: none;
        z-index: 3;

        @include media ($md) {
            display: block;
            z-index: 100;

            &.active {
                &::before {
                    transform: rotate(45deg);
                    top: 5px;
                    background: $white;
                }
                &::after {
                    transform: rotate(-45deg);
                    bottom: 5px;
                    background: $white;
                }
    
            }
        }

       
    }

    &__burger::before, &__burger::after {
        content: '';
        display: block;
        height: 2px;
        position: absolute;
        left: 0;
        width: 100%;
        background: $black;
        transition: 0.5s;

    }

    &__burger::before {
        top: 0;

        // &.active {
        //     transform: rotate(45deg);
        //     top: 5px;
        //     background: $white;
        // }

    }
    &__burger::after {
        bottom: 0;

        // &.active {
        //     transform: rotate(-45deg);
        //     bottom: 5px;
        //     background: $white;
        // }
    }


}